import {memo, ReactNode} from 'react';
import {classNames, Mods} from "shared/lib/classNames/classNames";
import cls from "./Review.module.scss"
interface ReviewProps {
    className?: string
    children?: ReactNode
    userPhoto :string
    userName: string
    reviewText: string

}


export const Review = memo((props: ReviewProps) => {
    const {
        className,
        children,
        userPhoto,
        userName,
        reviewText,
        ...otherProps
    } = props

    const mods: Mods = {};

    return (
        <div
            className={classNames(cls.Review, mods, [className])}
            {...otherProps}
        >
            <header className={cls.header}>Отзывы Наших клиентов</header>
            <img className={cls.userPhoto} src={userPhoto}/>
            <div>
                <div className={cls.userName}>{userName}</div>
                <div className={cls.reviewText}>{reviewText}</div>
            </div>
        </div>
    );
});