import {memo, ReactNode} from 'react';
import {classNames, Mods} from "shared/lib/classNames/classNames";
import {PageWrapper} from "shared/ui/PageWrapper/PageWrapper";
import Chart from "widgets/Chart/Chart";
import {saleChart} from "entity/SaleChart"
import {Carousel} from "react-responsive-carousel";
import cls from "./MainPage.module.scss"
import {postApi} from "shared/api/RtkService";
import {Review} from "widgets/Review/Review";

interface MainPageProps {
    className?: string
    children?: ReactNode
}


const MainPage = memo((props: MainPageProps) => {
    const {
        className,
        children,
        ...otherProps
    } = props

    const mods: Mods = {};
    const labels = [
        'приволжский ФО',
        'сибирский ФО',
        'северо-западный ФО',
        'уральский ФО',
        'северо-кавказский ФО',
        'дальневосточный ФО'
    ]
    const {data: dataUser, isLoading: isLoadingUser, error: errorUser} = postApi.useGetDataQuery({limit: 10, start: 0})
    const {data: dataComment, isLoading: isLoadingComment, error: errorComment} = postApi.useGetCommentQuery({
        limit: 10,
        start: 0
    })


    return (
        <PageWrapper>
            <div
                className={classNames('', mods, [className])}
                {...otherProps}
            >
                <Chart datasets={saleChart} labels={labels} typeChart={"bar"} label={"Продажи"}/>
                {children}
            </div>
            <Carousel className={cls.carusel} infiniteLoop={true} autoPlay={true}>
                    {dataComment && dataComment.map(comment => {
                        const user = dataUser?.find(user => user.id === comment.id);
                        const userName = user ? user.name : "Unknown User";

                        return (
                            <div key={comment.id}>
                                <Review
                                    userPhoto={"https://hornews.com/upload/images/blank-avatar.jpg"}
                                    userName={userName}
                                    reviewText={comment.body}
                                />
                            </div>
                        );
                    })}

            </Carousel>
        </PageWrapper>
    );
});
export default MainPage